import Vue from 'vue'
import Vuex from 'vuex'
 
//1.ÂÆâË£ÖÊèí‰ª∂
Vue.use(Vuex)
 
//2.ÂàõÂª∫ÂØπË±°
const store = new Vuex.Store({
  state:{
    logined: 0 ,
    userId: null ,
    onesay:'',
    total:0,
    imgId: 'default_img.jpg'
  },
  mutations:{
    changelogin(state,temp){
      state.logined = temp
    },

    changeUserId(state,id){
      state.userId = id
    },

    changeTotal(state,sum){
      state.total = Number(sum)
    },

    changeImgId(state,imgid){
      state.imgId = imgid
    },
    
    getOnesay(state,said){
      state.onesay = said
    },

    logout(state){
      state.logined = 0,
      state.userId = null,
      state.total = 0,
      state.imgId = 'default_img.jpg'
    },
  },
  actions:{
 
  },
  getters:{
 
  },
  modules:{
    
  }
})
//3.ÂØºÂá∫‰ΩøÁî®
export default store